<template>
  <div class="main-div">
    <div class="main-bg" @click="bgPressed">
      <div id="avator" v-bind:style="{ width: window.width + 'px', height: '210px'}"></div>
      <DialogueTheme id="theme" :msgs="msgs" :speaker="speaker" :finishConversation="finishConversation" v-if="showDialogue" />
    </div>
  </div>
</template>

<script>
import DialogueTheme from '@/components/DialogueTheme'

export default {
  name: 'EscalatorShowUp',
  components: {
    DialogueTheme
  },
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      },
      speaker: '手扶梯',
      msgs: ['嘖，我真沒想到新館員會是你⋯⋯這樣的傢伙，看來張中元現在人手很不足啊。',
        '哈哈，先別生氣，我不是針對你。既然只有你來，也就只能靠你了。',
        '你看到包圍著我的這個天井吧？有個厲害的傢伙在這布了個「轉乘陣」，沒時間解釋，你需要知道的，我都寫在這張筆記上了，你快過去看看吧。'],
      showDialogue: true
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
  },
  methods: {
    bgPressed () {
      this.showDialogue = true
    },
    finishConversation (msgs) {
      this.$router.push({name: 'CommuteTransferDiagramIntroduction'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/escalator-bg.png');
      position: relative;
      #avator {
        background-image: url('~@/assets/images/commute/escalator.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        position: absolute;
        bottom: 320px;
      }
      #theme {
        position: absolute;
        bottom: 100px;
      }
    }
  }
</style>



